@import url(https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,500;1,300;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@500;600;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@500;600;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Serif:wght@100;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@500;600;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  font-family: "Rubik", "Arial", sans-serif;
  font-weight: 500;
}
.logo::after {
  width: 100%;
  max-width: 30px;
  height: 6px;
  display: block;
  content: "";
  border-radius: 1000px;
  background: #4b6cc1;
  max-width: 30%;
  margin-top: 8px;
  transition: max-width ease 0.3s;
}

.logo {
  font-size: 1.75rem;
  display: block;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.063em;
  color: black;
}
.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2rem 0rem;
}
.navbar__links {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  list-style-type: none;
  width: 420px;
  color: #b4abab;
}
.navbar__links a {
  color: inherit;
  cursor: pointer;
}
.navbar__links a:hover {
  color: #535151;
  transition: all 0.25s ease-in;
}
.pdf__handle {
  background-color: inherit;
}
.resume__button {
  color: #fff;
  font-size: inherit;
  background-color: #4b6cc1;
  border: none;
  border-radius: 5px;
  padding: 8px 12px;
  cursor: pointer;
  transition: all 0.25s ease-in;
}
.resume__button:hover {
  background-color: #3d589d;
}
.resume__button a:hover {
  color: #fff;
}

a {
  text-decoration: none;
}
.container {
  padding: 0 3rem;
  margin: 0 auto;
  padding-bottom: 1rem;
}
.heading__md {
  font-size: 1.75rem;
  text-align: center;
  padding-top: 2rem;
  color: black;
  font-family: "Inter", sans-serif;
  font-weight: 800;
}
.subtitle__md {
  font-size: 0.85rem;
  text-align: center;
  color: gray;
  font-family: "Inter", sans-serif;
  font-weight: 600;
}

.footer {
  display: flex;
  flex-direction: row;
  padding: 1rem 0rem;
  color: #353535;
  justify-content: right;
  border-top: #5e5e5e 1px solid;
}

.card {
  font-family: "Inter", sans-serif;
  margin-bottom: 3rem;
  width: 200px;
  padding: 1rem 2rem;
  background-color: transparent;
  border: rgba(0, 0, 0, 0.08) 1px solid;
  border-radius: 6px;
  transition: box-shadow 0.3s ease-in-out;
  min-height: 200px;
}

.card h3 {
  font-size: 0.9rem;
  color: #4b6cc1;
}

.card h1 {
  font-size: 1.5rem;
  color: black;
}
.card p {
  font-size: 0.75rem;
  color: gray;
  line-height: normal;
}

.card:hover {
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.4);
}

.projects__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  justify-items: center;
  padding: 2rem;
}

p {
  font-family: "IBM Plex Serif", serif;
  font-size: 1.25rem;
  max-width: 75%;
  line-height: 3rem;
  font-weight: 400;
}
.about__wrapper {
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
  justify-items: center;
}
ul {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-items: left;
  list-style-type: none;
}

.landing__wrapper {
  display: flex;
  align-items: center;
  padding-bottom: 2rem;
}
.image__wrapper {
  padding-left: 5rem;
}
.rect {
  background-color: black;
  padding: 50px;
  margin-bottom: 50px;
  width: 100%;
  border-radius: 6px;
  max-width: 424px;
}
img {
  position: relative;
  max-width: 100%;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.05), 0 5px 30px rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  width: 100%;
}
.text__wrapper {
  font-family: "Inter", sans-serif;
  width: 45%;
  text-align: left;
  padding-left: 8rem;
  display: flex;
  flex-direction: column;
}
.jumbo__header {
  font-size: 3rem;
  letter-spacing: -2.4px;
  font-weight: 700;
}
.subtext__header {
  line-height: 2rem;
  font-size: 1.8rem;
  letter-spacing: -0.5px;
  font-weight: 500;
  width: 80%;
}
.subtext__header a {
  color: #4b6cc1;
}
.contact__wrapper {
  padding-top: 2rem;
}
.contact {
  font-weight: 400;
  font-size: 1.15rem;
}

.secondary {
  font-size: inherit;
  color: black;
  border: 1px solid gray;
  border-radius: 5px;
  padding: 8px 12px;
  cursor: pointer;
  transition: all 0.25s ease-in;
  background-color: transparent;
}
.secondary:hover {
  background: #4b6cc1;
  transition: all 0.25s ease-in;
}
.primary a {
  color: #fff;
}
.secondary:hover a {
  color: #fff;
  transition: all 0.25s ease-in;
}
.secondary a {
  color: black;
}
.primary {
  color: #fff;
  background-color: #4b6cc1;
  border: none;
  border-radius: 5px;
  padding: 8px 12px;
  cursor: pointer;
  transition: all 0.25s ease-in;
  font-size: 1rem;
}
.primary:hover {
  background-color: #3d589d;
}

.contact__center {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  align-items: center;
}


